@charset "UTF-8";

@import url("https://fonts.googleapis.com/css?family=Titillium+Web:200,200i,300,300i,400,400i,600,600i,700,700i,900&display=swap");
@import url("./_reset.scss");

a {
  text-decoration: none;
  color: inherit;
}

.cf::before,
.cf::after {
  content: "";
  display: block;
  clear: both;
}

body {
  font: 30px "Titillium Web", sans-serif;
}

form input {
  width: 50%;
  background: none;
  font-size: 30px;
  text-align: center;
  color: #fff;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;

  &::placeholder {
    text-align: center;
    color: #fff;
    font-family: "Titillium Web", sans-serif;
  }
}

#App {
  position: relative;
  height: 100vh;
  background-size: cover;
}

.content {
  position: absolute;
  left: 50%;
  top: 50%;
  width: auto;
  padding: 30px 50px;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  text-align: center;
  color: #fff;

  .weather {
    display: block;
  }

  .clockWrap {
    padding: 10px 0;

    & .dateText {
      font-size: 50px;
    }
    & .timeText {
      line-height: 1;
      font-size: 100px;
    }
  }

  .userWrap {
    padding: 10px 0;
    & p {
      font-weight: 700;
    }
    & form input {
      width: 50%;
    }
  }

  .listWrap {
    padding: 10px 0;
    & form {
      width: 100%;
    }
    & form input {
      width: 80%;
    }
    & > article {
      float: left;
      width: 48%;
      margin: 10px 0;
      font-size: 20px;
    }
    & > article + article {
      margin-left: 4%;
    }
    & > article h4 {
      font-size: 20px;
    }
    & > article ul li {
      position: relative;
      margin: 5px 0;
      padding: 5px;
      text-align: left;
      border-bottom: 1px solid #fff;
    }
    & li button {
      position: absolute;
      top: 50%;
      right: 5px;
      width: 20px;
      height: 20px;
      background: none;
      cursor: pointer;
      border: none;
      color: #fff;
      transform: translateY(-50%);
    }
    & li button:nth-child(2) {
      right: 30px;
    }
    & li button:active {
      background: rgba(0, 0, 0, 0.4);
    }
  }
}
